.home .privacy {
  margin-top: -75px;
}
.home article {
  display: block;
  position: relative;
  max-width: 780px;
  max-height: 650px;
  width: calc(100vw - 20px);
  height: calc(100vh - 300px);
  margin: 20px auto 0;
  text-align: left;
  background: var(--box-bg);
  padding: 40px 10px 30px 30px;
}
.home p {
  font-size: 14px;
}
.home p strong {
  font-weight: 600;
}
.home p a {
  color: var(--body);
  text-decoration: underline;
}
.home h2,
.home h3 {
  display: inline-block;
  font-size: 18px;
  margin: 18px 0;
}
.home h6 {
  text-align: center;
  margin-top: 10px;
  font-size: 20px;
  font-family: "ProximaNova-Regular";
  border-bottom: 1px solid #dcab49;
  padding-bottom: 15px;
  display: inline-block;
}
.home h5 {
  text-align: left;
  font-size: 20px;
  font-family: "ProximaNova-Regular";
  padding-bottom: 15px;
  display: inline-block;
  width: 280px;
  margin-top: 40px;
}
.home .custom-select {
  position: relative;
  font-family: Arial;
  background: gray;
  width: 300px !important;
  height: 60px;
  border: 1px solid #eaac32;
  display: inline-block;
}
.home .custom-select select {
  display: none;
  /*hide original SELECT element:*/
}
.home .select-selected {
  background-color: #eaac32;
}
.home .select-selected:after {
  position: absolute;
  content: "";
  top: 25px;
  right: 10px;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-color: #fff transparent transparent transparent;
}
.home .select-selected.select-arrow-active:after {
  border-color: transparent transparent #fff transparent;
  top: 15px;
}
.home .select-items div,
.home .select-selected {
  color: #ffffff;
  padding: 12px 16px;
  cursor: pointer;
  user-select: none;
}
.home .select-items {
  position: absolute;
  background-color: #eaac32;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.home .select-hide {
  display: none;
}
.home .select-items div:hover,
.home .same-as-selected {
  background-color: rgba(0, 0, 0, 0.1);
}
.home li {
  font-size: 14px;
  line-height: 21px;
  margin: 0 0 4px 24px;
  list-style: circle;
}

