.cont {
  margin-top: 30px;
  display: inline-block;
  text-decoration: none;
  color: #fff;
  transition: all 0.5s;
  cursor: pointer;
}

.cont:hover {
  text-decoration: none;
  color: #dcab49;
}

.cont span {
  color: #dcab49;
}

.activeDot {
  background-color: #011327;
}