@font-face {
  font-family: "ProximaNova-Bold";
  src: url("~fonts/ProximaNova-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "ProximaNova-Regular";
  src: url("~fonts/ProximaNova-Regular.ttf") format("truetype");
}
body,
input,
button {
  font-family: "ProximaNova-Bold";
  -webkit-font-smoothing: antialiased;
}

h1 {
  font-size: 32px;
  font-weight: 200;
  margin-bottom: 26px;
}

p {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;
}

strong {
  font-weight: 400;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

body {
  color: var(--body);
  background: var(--body-bg);
}
@media print {
  body {
    background: #fff;
  }
}